export default {
	data() {
		return {
			topic:{
				list:[]
			}
		};
	},
	onLoad() {
		this.__init();
	},
	onShow() {
		this.getFollowPostList();
	},
	methods:{
		// 初始化数据
		__init(){
			this.getSwiper();
		},
	
		
		// 获取广告
		async getSwiper(){
			let [err,res] = await this.$http.get('/adsense/0');
			if (!this.$http.errorCheck(err,res)) return;
			let arr = [];
			let list = res.data.data.list;
			for (let i = 0; i < list.length; i++) {
				arr.push({
					src:list[i].src,
					url:list[i].url
				})
			}
			this.topic.swiper = arr;
		},
	}
}
